/* Стилизация ползунка скроллбара */
::-webkit-scrollbar-thumb {
    background: #c5c5c5;
    width: 2rem;
    height: 1rem;
    border-radius: 1rem;
}

/* Стилизация скроллбара при наведении */
::-webkit-scrollbar-thumb:hover {
    background: #a9a9a9;
    cursor: pointer;
}

